"use client";

import { motion } from "framer-motion";

const Stripe = ({ d, stroke, delay }: { d: string; stroke: string; delay: number }) => (
  <motion.path
    d={d}
    stroke={stroke}
    strokeWidth="30"
    fill="none"
    initial={{ pathLength: 0 }}
    animate={{ pathLength: 1 }}
    transition={{ duration: 2, ease: "easeInOut", delay }}
  />
);

export default function Background() {
  return (
    <svg
      className="absolute inset-0 w-full h-full"
      viewBox="0 0 400 800"
      preserveAspectRatio="xMidYMid slice"
    >
      {/* Arched stripes for the new design */}
      <Stripe d="M 0,0 C 150,150 250,150 400,0" stroke="var(--swirl-green)" delay={0} />
      <Stripe d="M 0,0 C 120,120 280,120 400,0" stroke="var(--swirl-purple)" delay={0.2} />
      <Stripe d="M 0,0 C 90,90 310,90 400,0" stroke="var(--swirl-yellow)" delay={0.4} />
    </svg>
  );
}