"use client";

import { useState } from "react";
import { motion, AnimatePresence, Transition } from "framer-motion";
import Background from "@/components/Background";

type AuthView = 'landing' | 'choice' | 'signup' | 'login';

const viewVariants = {
  initial: { opacity: 0, y: 20 },
  animate: { opacity: 1, y: 0 },
  exit: { opacity: 0, y: -20 },
};

const transition: Transition = { duration: 0.4, ease: "easeInOut" };

const UserIcon = () => <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path><circle cx="12" cy="7" r="4"></circle></svg>;
const MailIcon = () => <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path><polyline points="22,6 12,13 2,6"></polyline></svg>;
const LockIcon = () => <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><rect x="3" y="11" width="18" height="11" rx="2" ry="2"></rect><path d="M7 11V7a5 5 0 0 1 10 0v4"></path></svg>;
const EyeIcon = () => <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path><circle cx="12" cy="12" r="3"></circle></svg>;
const GoogleIcon = () => <svg width="24" height="24" viewBox="0 0 24 24"><path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/><path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/><path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l3.66-2.84z"/><path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/><path fill="none" d="M1 1h22v22H1z"/></svg>;

const NeumorphicButton = ({ onClick, children, className }: { onClick?: () => void; children: React.ReactNode; className?: string }) => (
    <button onClick={onClick} className={`w-full max-w-sm py-4 rounded-full text-lg font-medium text-text-secondary shadow-neumorphic-outer active:shadow-neumorphic-inner transition-all duration-150 ${className}`}>
        {children}
    </button>
);

const AuthInput = ({ type, placeholder, icon, hasEye }: { type: string; placeholder: string; icon: React.ReactNode; hasEye?: boolean; }) => (
  <div className="relative w-full mb-4">
    <span className="absolute inset-y-0 left-0 flex items-center pl-4 text-brand-text-light">{icon}</span>
    <input
      type={type}
      placeholder={placeholder}
      className="w-full py-3 pl-12 pr-12 text-brand-text-dark bg-brand-cream rounded-full focus:outline-none focus:ring-2 focus:ring-brand-blue-dark"
    />
    {hasEye && <span className="absolute inset-y-0 right-0 flex items-center pr-4 text-brand-text-light"><EyeIcon /></span>}
  </div>
);

const TopContent = () => (
  <div className="relative text-center pt-24 pb-12 px-6">
    <div className="absolute inset-0 -z-10 overflow-hidden">
        <Background />
    </div>
    <div className="relative z-10">
      <h1 className="text-2xl font-serif text-brand-text-dark">Welcome to</h1>
      <h2 className="text-7xl font-extrabold font-serif text-brand-text-dark -mt-2">Googly</h2>
      <p className="italic text-brand-text-light mt-2 text-sm">“Proofread. Analyze. Simplified.”</p>
    </div>
  </div>
);

export default function Home() {
  const [view, setView] = useState<AuthView>('landing');

  const renderContent = () => {
    switch (view) {
      case 'landing':
        return (
          <motion.div key="landing" variants={viewVariants} transition={transition} className="w-full flex flex-col items-center">
            <p className="text-sm text-white text-center mb-6 max-w-xs">Sign in to upload, analyze, and ask questions–all secure, all private</p>
            <button onClick={() => setView('choice')} className="w-full max-w-sm bg-brand-blue-dark text-white py-3 rounded-full text-lg font-medium shadow-lg hover:opacity-90 transition">Continue</button>
          </motion.div>
        );
      case 'choice':
        return (
          <motion.div key="choice" variants={viewVariants} transition={transition} className="w-full flex flex-col items-center">
            <button className="w-full max-w-sm bg-white text-gray-700 py-3 rounded-full text-base font-medium shadow-lg hover:bg-gray-50 transition flex items-center justify-center mb-4">
              <GoogleIcon /><span className="ml-3">Continue with Google</span>
            </button>
            <div className="flex items-center w-full max-w-sm my-2"><hr className="flex-grow border-t border-white/50" /><span className="px-3 text-sm text-white/90">OR</span><hr className="flex-grow border-t border-white/50" /></div>
            <button onClick={() => setView('signup')} className="w-full max-w-sm bg-brand-blue-dark text-white py-3 rounded-full text-lg font-medium shadow-lg hover:opacity-90 transition mt-4">Create Account</button>
            <p className="text-sm text-white mt-6">Already have an account? <button onClick={() => setView('login')} className="font-bold text-white hover:underline">Log In</button></p>
          </motion.div>
        );
      case 'signup':
        return (
          <motion.div key="signup" variants={viewVariants} transition={transition} className="w-full flex flex-col items-center">
            <h3 className="text-3xl font-bold text-white mb-6">Create Account</h3>
            <div className="w-full max-w-sm">
              <AuthInput type="text" placeholder="Full Name" icon={<UserIcon />} />
              <AuthInput type="email" placeholder="Email Address" icon={<MailIcon />} />
              <AuthInput type="password" placeholder="Password" icon={<LockIcon />} hasEye />
            </div>
            <button className="w-full max-w-sm bg-brand-blue-dark text-white py-3 rounded-full text-lg font-medium shadow-lg hover:opacity-90 transition mt-2">Create Account</button>
            <p className="text-xs text-white/80 text-center mt-4 max-w-xs">By signing up, you agree to our Terms, Privacy Policy, and Cookie Use.</p>
          </motion.div>
        );
      case 'login':
        return (
          <motion.div key="login" variants={viewVariants} transition={transition} className="w-full flex flex-col items-center">
            <h3 className="text-3xl font-bold text-white mb-6">Login</h3>
            <div className="w-full max-w-sm">
              <AuthInput type="email" placeholder="Email Address" icon={<MailIcon />} />
              <AuthInput type="password" placeholder="Password" icon={<LockIcon />} hasEye />
            </div>
            <a href="#" className="text-sm text-white self-end mt-1 mb-4 mr-4 hover:underline">Forgot Password?</a>
            <button className="w-full max-w-sm bg-brand-blue-dark text-white py-3 rounded-full text-lg font-medium shadow-lg hover:opacity-90 transition">Login</button>
          </motion.div>
        );
    }
  };

  return (
    <div className="relative w-full min-h-screen bg-brand-cream overflow-hidden">
      {/* This container holds both sections and manages the layout */}
      <div className="relative flex flex-col min-h-screen">
        
        {/* Top Cream Section */}
        <section className="relative z-10 pt-24 pb-28 px-6 text-center bg-transparent">
          <div className="absolute inset-0 -z-20">
            <Background />
          </div>
          <h1 className="text-2xl font-serif text-brand-text-dark">Welcome to</h1>
          <h2 className="text-7xl font-extrabold font-serif text-brand-text-dark -mt-2">Googly</h2>
          <p className="italic text-brand-text-light mt-2 text-sm">“Proofread. Analyze. Simplified.”</p>
        </section>

        {/* Bottom Blue Section */}
        <section className="relative flex-grow flex flex-col items-center px-6 pb-8 bg-brand-blue-light">
          {/* This SVG creates the curved top edge */}
          <div className="absolute bottom-full left-0 w-full h-20 text-brand-blue-light">
            <svg viewBox="0 0 100 100" preserveAspectRatio="none" className="w-full h-full fill-current">
              <path d="M0,100 C40,0 60,0 100,100 Z" />
            </svg>
          </div>
          
          {/* Content inside the blue section */}
          <div className="w-full max-w-sm">
            <AnimatePresence mode="wait">
              {renderContent()}
            </AnimatePresence>
          </div>
        </section>

      </div>
    </div>
  );
}
